					}
				}
				
				.product-price {
					display: flex;
					align-items: center;
					gap: 10rpx;
					
					.current-price {
						font-size: 32rpx;
						font-weight: bold;
						color: #FFB6C1;
					}
					
					.original-price {
						font-size: 24rpx;
						color: #999;
						text-decoration: line-through;
					}
				}
			}
		}
	}
	
	/* Êï∞ÈáèÈÄâÊã© */
	.quantity-section {
		background-color: #fff;
		margin-bottom: 20rpx;
		padding: 30rpx;
		
		.quantity-control {
			display: flex;
			align-items: center;
			gap: 30rpx;
			
			.quantity-btn {
				width: 60rpx;
				height: 60rpx;
				background-color: #f0f0f0;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				font-size: 32rpx;
				color: #333;
				
				&:disabled {
					background-color: #f5f5f5;
					color: #ccc;
				}
			}
			
			.quantity-text {
				font-size: 32rpx;
				font-weight: bold;
				color: #333;
				min-width: 80rpx;
				text-align: center;
			}
		}
	}
	
	/* Êî∂Ë¥ßÂú∞ÂùÄ */
	.address-section {
		background-color: #fff;
		margin-bottom: 20rpx;
		padding: 30rpx;
		
		.address-card {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 20rpx;
			background-color: #f8f8f8;
			border-radius: 12rpx;
			
			.address-info {
				flex: 1;
				
				.address-header {
					display: flex;
					align-items: center;
					gap: 20rpx;
					margin-bottom: 10rpx;
					
					.contact-name {
						font-size: 28rpx;
						font-weight: bold;
						color: #333;
					}
					
					.contact-phone {
						font-size: 24rpx;
						color: #666;
					}
				}
				
				.address-detail {
					font-size: 26rpx;
					color: #666;
					line-height: 1.4;
				}
			}
			
			.no-address {
				flex: 1;
				text-align: center;
				
				.no-address-text {
					font-size: 26rpx;
					color: #999;
					display: block;
					margin-bottom: 10rpx;
				}
				
				.add-address-btn {
					font-size: 24rpx;
					color: #FFB6C1;
				}
			}
			
			.arrow {
				font-size: 24rpx;
				color: #999;
			}
		}
	}
	
	/* ÈÖçÈÄÅÊñπÂºè */
	.delivery-section {
		background-color: #fff;
		margin-bottom: 20rpx;
		padding: 30rpx;
		
		.delivery-options {
			display: flex;
			flex-direction: column;
			gap: 15rpx;
			
			.delivery-option {
				display: flex;
				align-items: center;
				justify-content: space-between;
				padding: 20rpx;
				border: 2rpx solid #f0f0f0;
				border-radius: 12rpx;
				transition: all 0.3s ease;
				
				&.active {
					border-color: #FFB6C1;
					background-color: #fef8fa;
				}
				
				.delivery-name {
