					color: #333;
				}
				
				.delivery-desc {
					font-size: 24rpx;
					color: #666;
					margin-left: 20rpx;
				}
				
				.delivery-price {
					font-size: 28rpx;
					font-weight: bold;
					color: #FFB6C1;
				}
			}
		}
	}
	
	/* ÊîØ‰ªòÊñπÂºè */
	.payment-section {
		background-color: #fff;
		margin-bottom: 20rpx;
